body {
  text-align: center;
}

table { border-collapse: collapse; }
table tr td, table tr th { padding: 0; }

.exercise__header {
  padding: 30px 0;
}

.exercise__answers-list {
  list-style: none; 
  padding: 0;
}

.exercise__active-note {
  font-weight: bold;
}

.exercise__answers-list-item {
  display: inline-block;
}

.list-item_wrong button {
  background-color: palevioletred;
}

.list-item_correct button {
  background-color: palegreen;
}

.keyboard__container {

}

.keys-list {
  list-style: none;
  padding: 0;
  position: relative;
  border-top: 2px solid #000;
}

.keys-list__key_white,
.keys-list__key_black {
  display: inline-block;
  vertical-align: top;
  border-color: #efefef;
  border-width: 0px 2px 2px 2px;
  border-style: solid;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}

.keys-list__key_white {
  background-color: white;
  height: 200px;
  width: 35px;
}

.keys-list__key_white:active {
  background-color: #efefef;
}

.keys-list__key_black {
  background-color: black;
  height: 120px;
  width: 20px;
  border-color: black;
  position: absolute;
  top: 0;
  left: auto;
  margin-left: -11px;
}

.keys-list__key_black:active {
  background-color: #303030;
}

.key_wrong {
  background-color: palevioletred;
}

.key_correct {
  background-color: palegreen;
}


.fretboard__container {
  display: flex;
  justify-content: center;
  padding: 20px 0; 
}


.fretboard__fret-button {
  background-color: burlywood;
  border: 0;
  border-left: 1px solid silver;
  border-right: 1px solid silver;
  width: 40px;
  height: 20px;
  display: block;
  position: relative;
}

.fret-button_correct {
  background-color: palegreen;
}

.fret-button_wrong {
  background-color: palevioletred;
}

.fretboard__fret-button:after {
  content: '';
  width: 100%;
  height: 2px;
  background: #efefef;
  top: 50%;
  left: 0;
  position: absolute;
}

.fretboard__fret:nth-child(1) .fretboard__fret-button {
  border-right: 4px solid brown;
}